.pop-up-container {
  width: 100%;
  height: 100%;
  color: #fff;
  z-index: 999;
  background-color: rgba(17, 17, 17, .592);
  display: none;
  position: fixed;
  top: 0;
}

.pop-up {
  width: 846px;
  height: 598px;
  background-color: #111;
  border-radius: 20px;
  gap: 20px;
  display: flex;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
