  color: #fff;
  background-color: #111;
  border: none;
  border-radius: 74px;
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
  position: relative;
}

.gradient-box {
  width: 140px;
  margin-top: 24px;
  position: relative;
}

.gradient-box:after, .gradient-box:before {
  content: "";
  box-sizing: border-box;
  width: calc(100% + 2px);
  height: calc(100% + 2px);
  -webkit-mask-composite: destination-out;
  z-index: 0;
  background-image: linear-gradient(to top, #ffc226, #f84119);
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 74px;
  position: absolute;
  top: -1px;
  right: -1px;
  mask-composite: exclude;
}

